@import url('https://fonts.googleapis.com/css2?family=KoHo:wght@200&family=Newsreader:wght@200&family=Poppins:wght@300;400;700&display=swap');

.footer{
    color: #FFFFFF99;
    font-family: 'Poppins', sans-serif;
    width: 100%;
    height: 10rem;
    overflow: hidden;
    margin-top: 2rem;
    text-align: center;
    padding: 0;
}

.footer p{
    color: rgba(255, 255, 255, 0.5);
    font-family: 'Poppins', sans-serif;
    width: 60%;
    height: 100%;
    text-align: center;
    overflow: hidden;
    margin: auto;
    padding: 0;
}

.footer ul{
    height: 3rem;
    list-style: none;
    display: flex;
    flex-direction : row;
    flex-wrap: wrap;
    justify-content : center;
    align-items : center;
    padding: 0;
}


.footer li{
    color: white;
    height: 100%;
    width: 10rem;
    padding: 0;
}

.footer a{
    text-decoration: none ;
    color: white;
    height: 100%;
    padding: 0;
}

.skilliconsimage{
    color: #ccc;
    height: 2.5rem;
    width: 100%;
    margin: 0; 
}

.skilliconsimage:hover,
.skilliconsimage:active{
    background: #2f89fc;
}



@media (max-width: 48rem)
{
    .footer{
        height: 10rem;
        width: 100%;
        padding: 0;
    }
    
    .footer p{
        width: 100%;
        margin: auto;
        padding: 0;
    }
    
    .footer ul{
        width: 100%;
        margin: auto;
        padding: 0;
    }
    .footer li{
        height: 100%;
        width: 5rem;
        padding: 0;
    }
}