@import url('https://fonts.googleapis.com/css2?family=KoHo:wght@200&family=Newsreader:wght@200&family=Poppins:wght@300;400;700&display=swap');

.about{
    color: #FFFFFF99;
    font-weight: bold;
    text-align: right;
    width: 100%;
    height: 50rem;
    position: relative;
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-evenly;
    align-items: center;
}
.about h1{
    position: absolute;
    top: -10rem;
    right: 1rem;
    opacity: 0.1;
    font-family: 'Poppins', sans-serif;
    font-size: 11rem;
    z-index: 0;
}

.details{
    /* border: 2px solid white; */
    width: 30rem;
    height: 45rem;
    text-align: left;
}


.details h2{
    font-family: 'Poppins', sans-serif;
    font-weight: 300;
    font-size: 2rem;
    letter-spacing: 1px;
    color: #FFFFFF;
    margin-top: 3rem;
    margin-right: 7rem;
    margin-bottom: 0;
}

.details h3{
    text-align: left;
    color: #8c8c8c;
    font-family: 'Poppins', sans-serif;
    font-size: 1.2rem;
    line-height: 1.8;
    font-weight: 400;
    letter-spacing: 1px;
    margin-bottom: 1rem;
} 

.details a{
    text-decoration: none;
    color:#2f89fc;
    font-size: 1.2rem;
    line-height: 1.8;
    font-weight: 400;
} 


.details h5{
    line-height: 1.5;
    color: #fff;
    font-size: 1.25rem;
    font-weight: 400;
    margin:0;
    margin-top:3rem;
    margin-bottom:1rem;
}

.details h4{
    line-height: 1.5;
    color: #8c8c8c;
    font-size: 1.25rem;
    font-weight: 400;
    margin:0;
    margin-bottom:1rem;
}

.details p{
    display: inline-block;
    margin-top: 0;
    margin-bottom: 1rem;
    color:#2f89fc;
    font-size: 1.2rem;
    line-height: 1.8;
    font-weight: 400;
}

.image{
    width: 23rem;
    height: 36rem;
    background: url("../../assets/likhan_image.jpg") center/cover no-repeat border-box ,  #FFFFFF;
    border-radius: 15px;

}

@media (max-width: 86rem){
    .about{
        width: 100%;

    }

    .details{
        position: static;
        width: 45%;
        height: 90%;
    }
    .image{
        width: 18rem;
        height: 26rem;
    }
    
}


@media (max-width: 48rem){
    .about h1{
        position: absolute;
        top: -5rem;
        right: 0;
        font-size: 6rem;
    }

    .detailsContainer{
        position: static;
        width: 100%;
        height: 100%;
        flex-direction: column;
    }

    .details{
        position: static;
        width: 100%;
    }

    .details h2{
        font-size: 1.8rem;
        text-align: center;
        letter-spacing: 1px;
        width: 100%;
        
    }

    .details h3{
        padding: 0;
        margin: 0 auto;
        margin-top: 2rem;
        font-size: 1rem;
        width: 20rem;
        height: 25rem;
        text-align: center;
    } 

    .details a{
        margin-left: 1rem;
    } 

    .details ul{
        margin-left: 1rem;
    }

    .details h5{
        margin-left: 1rem;
    }

    .details h4{
        margin-left: 1rem;
    }
    
    .image{
        display: none;
    }
    
}


@keyframes getclose {
    0% {
        transform: translateY(0rem);
     }
        25% {
            transform: translateY(-0.5rem);
        }
        50% {
            transform: translateY(-1rem); 
        }
        75% {
            transform: translateY(-1.1rem);
        }
        100% {
            transform: translateY(-00.5rem);
        }
      }
    
